<template>
  <div class="row">
    <div class="col-md-6">
      <header-component header="Alterar cadastro" titulo="Mantenha seu cadastro sempre atualizado"></header-component>
      <form class="form-row mb-0">
        <div class="col-md-12 col-lg-12">
          <div class="form-group col-md-12 col-lg-12">
            <label class="control-label" for="cpf">CPF</label>
            <the-mask class="form-control" v-model="form.cpf" :mask="['###.###.###-##']" disabled/>
            <span class="invalido text-warning">{{ msgValidacao }}</span>
          </div>
          <input-component classediv="form-group col-md-12 col-lg-12" labelinput="nome" label="Nome" classeinput="form-control"
          tipoinput="text" v-model="form.nome"></input-component>
          <input-component classediv="form-group col-md-12 col-lg-12" labelinput="email" label="E-mail" classeinput="form-control"
          tipoinput="email" v-model="form.email"></input-component>
          <div class="form-group col-md-12 col-lg-12">
            <label class="control-label" for="telefone">Telefone</label>
            <the-mask class="form-control" type="tel" v-model="form.telefone" :mask="['(##) #####-####']" required/>
          </div>
          <div class="form-group col-md-12 col-lg-12 text-center">
            <button-component tipo="button" classe="btn btn-default" rotulo="Salvar" @click.native="alterarCadastro"></button-component>
          </div>
        </div>
      </form>
    </div>
    <div class="col-md-6 col-lg-6">
      <header-component header="Alterar senha" titulo="Troque a sua senha quando desejar"></header-component>
      <form class="form-row mb-0">
        <div class="col-md-12 col-lg-12">
          <input-component classediv="form-group col-md-12 col-lg-12" labelinput="senha" label="Senha atual" classeinput="form-control"
          tipoinput="password" v-model="form.senha"></input-component>
          <input-component classediv="form-group col-md-12 col-lg-12" labelinput="senhaConfirm" label="Confirmar senha atual" classeinput="form-control"
          tipoinput="password" v-model="form.senhaConfirm"></input-component>
          <input-component classediv="form-group col-md-12 col-lg-12" labelinput="novaSenha" label="Nova senha" classeinput="form-control"
          tipoinput="password" v-model="form.novaSenha"></input-component>
          <input-component classediv="form-group col-md-12 col-lg-12" labelinput="novaSenhaConfirm" label="Confirmar nova senha" classeinput="form-control"
          tipoinput="password" v-model="form.novaSenhaConfirm"></input-component>
          <div class="form-group col-md-12 col-lg-12 text-center">
            <button-component tipo="button" classe="btn btn-default" rotulo="Alterar senha" @click.native="alterarSenha"></button-component>
          </div>
        </div>
        <modal-component v-if="showModal" @close="showModal = false">
          <h3 slot="header" class="mx-auto">{{header}}</h3>
          <h5 slot="body" class="text-center"><i :class="icone" aria-hidden="true"></i> {{mensagem}}</h5>
        </modal-component>
      </form>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  import CPF from 'gerador-validador-cpf'
  import HeaderComponent from '../shared/HeaderComponent.vue'
  import InputComponent from '../shared/InputComponent.vue'
  import ButtonComponent from '../shared/ButtonComponent.vue'
  import ModalComponent from '../shared/ModalComponent.vue'
  export default {
    name: 'alterar-cadastro-form-component',
    components: {
      HeaderComponent, InputComponent, ButtonComponent, ModalComponent
    },
    data(){
      return{
        header: '', icone: '', mensagem: '', showModal: false, msgValidacao: '', usuario: false,
        form: { cpf: null, nome: null, telefone: null, email: null, senha: null, senhaConfirm: null, token: null, novaSenha: null, novaSenhaConfirm: null }
      }
    },
    methods: {
      alterarCadastro (event){
        this.header = 'Alterar Cadastro'
        this.showModal = true
        this.mensagem = ''
        this.icone = ''
        if (this.form.nome && this.form.telefone && this.form.email) {
          axios.post('http://bankscore.com.br/rest/api/editarUsuarioConsumidor', { //aqui põe a url da API
            cpf: this.form.cpf,
            name: this.form.nome,
            phone: this.form.telefone,
            email: this.form.email,
            token: sessionStorage.getItem('token')
          })
          .then(response => {
            sessionStorage.setItem('usuario', JSON.stringify(response.data.user))
            this.mensagem = 'Alteração de cadastro realizada com sucesso'
            this.icone = 'text-success far fa-lg fa-check-circle'
          })
        }
        else {
          this.mensagem = 'Preencha todos os campos'
          this.icone = 'text-danger far fa-lg fa-times-circle'
          event.preventDefault()
        }
      },
      alterarSenha (event){
        this.header = 'Alterar Senha'
        this.showModal = true
        this.mensagem = ''
        this.icone = ''
        if(this.form.senha != this.form.senhaConfirm){
          this.mensagem = 'Os campos de senha e confirmar senha devem ser iguais'
          this.icone = 'text-danger far fa-lg fa-times-circle'
          event.preventDefault()
        }else if(this.form.novaSenha != this.form.novaSenhaConfirm){
          this.mensagem = 'Os campos de nova senha e confirmar nova senha devem ser iguais'
          this.icone = 'text-danger far fa-lg fa-times-circle'
          event.preventDefault()
        }else{
          axios.post('http://bankscore.com.br/rest/api/editarSenha', { //aqui põe a url da API
            password: this.form.senha,
            password_confirmation: this.form.senhaConfirm,
            novaSenha: this.form.novaSenha,
            novaSenhaConfirm: this.form.novaSenhaConfirm,
            token: sessionStorage.getItem('token')
          })
          .then(response => {
            sessionStorage.setItem('usuario', JSON.stringify(response.data.user))
            this.mensagem = response.message
            this.icone = 'text-success far fa-lg fa-check-circle'
          })
        }
      }
    },
    created(){
      let usuarioAux = sessionStorage.getItem('usuario') //pega a info de "usuario"
      if (usuarioAux){
        this.usuario = JSON.parse(usuarioAux) //this.usuario recebendo as infos de usuario em forma de objeto
        this.form.cpf = this.usuario.cpf
        this.form.nome = this.usuario.name
        this.form.telefone = this.usuario.phone
        this.form.email = this.usuario.email
      }
    }
  }
</script>

